.halidom-item {
  @extend %not-select;

  display: grid;
  grid-template-columns: 80px 20% 1fr;
  align-items: center;
  justify-items: center;

  border: thin solid #eec2a2;
  box-sizing: border-box;

  .name {
    justify-self: left;
    min-width: 0;
    max-width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
