$slider-height: 50px;
$border-width: 1px;
$thumb-height: $slider-height - 2 * $border-width - 2;

.slider {
  @extend %not-select;

  display: grid;
  grid-template-columns: 50px 1fr 50px;
  align-items: center;
  justify-items: center;

  width: 100%;
}

.slider-main {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  border: $border-width solid $black2;
  border-radius: 4px;
  box-sizing: border-box;

  width: 100%;
  height: $slider-height;

  background-color: aliceblue;
  cursor: pointer;

  touch-action: none;
}

.slider-btn {
  width: 40px;
  height: 40px;
  filter: drop-shadow(0 1px 1px $black0) invert(0.3);
}

.slider-rail,
.slider-track {
  position: absolute;
  height: 10px;
}

.slider-rail {
  width: 100%;
  background-color: $base1;
}

.slider-track {
  background-color: $blue0;
}

.slider-thumb {
  position: absolute;
  top: 1px;

  border-radius: 4px;
  width: 30px;
  height: $thumb-height;

  background-color: $black1;

  line-height: $thumb-height;
  font-weight: bold;
  text-align: center;
  color: white;
}
